import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Socket } from 'socket.io-client';
import { DialogService } from 'ng-metro4';
import { users } from './dashboard/model/users/users.config';
declare const Metro: any;
@Injectable({
  providedIn: 'root'
})
export class AppService {
  private activity: any;
  socket: Socket;
  user: users;
  openCalc: boolean = false;
  constructor(public actRoute: ActivatedRoute, public router: Router, public http: HttpClient, public dialogService: DialogService) { }
  folders() {
    window.open("/files")
  }
  wait(o: boolean = true) {
    if (this.activity == undefined && o) {
      this.activity = Metro.activity.open({
        type: 'square',
        style: "color",
        text: '<div class=\'mt-2 text-small fg-white\'>من فضلك انتظر...</div>',
      });
    } else {
      Metro.activity.close(this.activity);
      this.activity = undefined;
    }
  }
  log(log: string) {
    this.dialogService.alert("خطا", `
      <p>${log}</p>
    `, "alert", "تمام", "alert");
  }
  isLogin() {
    return !(this.user == undefined);
  }
  checkLogin() {
    if (!this.isLogin()) {
      this.router.navigateByUrl("login");
      return false;
    }
    return true;
  }
  logOut() {
    this.dialogService.confirm(
      "تاكيد تسجيل الخروج",
      "هل انت متاكد من تسجيل الخروج",
      "نعم",
      "لا").subscribe(
        res => {
          if (res == true) {
            if (this.router.url == "/addPost" || this.router.url.startsWith("/dashboard")) {
              this.router.navigateByUrl("")
            }
            sessionStorage.clear();
            this.user = undefined;
          }
        }
      )
  }
  img(id) {
    if (!id) id = "mah";
    return `/api/files/icon/${id}`;
  }
  showUser() {
    return JSON.parse(sessionStorage.getItem('lawerAssistant')).type == 'admin';
  }
}
