import { Component, OnInit } from '@angular/core';
import { AppService } from './app.service';
import { NavigationStart, Event, NavigationEnd } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  constructor(private app: AppService) { }

  ngOnInit() {
    if (sessionStorage.getItem("lawerAssistant") != null) {
      this.app.user = JSON.parse(sessionStorage.getItem("lawerAssistant"));
    }
    this.app.router.events
      .subscribe(
        (event: Event) => {
          if (event instanceof NavigationStart) {
            if (event.url.startsWith("/dashboard") || event.url == "/addPost") {
              if (!this.app.isLogin()) {
                this.app.router.navigateByUrl("/404")
              }
            } else {
              this.app.wait();
            }
          } else if (event instanceof NavigationEnd) {
            this.app.wait(false)
          }
        });
  }
}
